import * as PIXI from 'pixi.js';

import { Vec2 } from './Vector';
import { setGlobalVariables, I, diffuse, advect, project } from "./utils";

export class Grid {
  constructor(app, options) {
    this.app = app;

    // initial options that gets filled with parameter
    this.options = Object.assign({
      dt: 0.001,
      diffusion: 0.0005,
      viscosity: 0.00000001
    }, options);

    // mobile test
    this.isMobile = PIXI.utils.isMobile.any;

    // for pathfindig
    this.tileSize = 13;
    this.graphics = new PIXI.Graphics();

    // dimentions
    this.W = Math.ceil(this.app.screen.width / this.tileSize);
    this.H = Math.ceil(this.app.screen.height / this.tileSize);

    // time step
    this.dt = this.options.dt;
    // fluid properties
    this.diff = this.options.diffusion;
    this.visc = this.options.viscosity;

    this.s = new Array(this.W*this.H).fill(0);
    this.density = new Array(this.W*this.H).fill(0);

    // current velocities
    this.Vx = new Array(this.W*this.H).fill(0);
    this.Vy = new Array(this.W*this.H).fill(0);

    // previous velocities
    this.Vx0 = new Array(this.W*this.H).fill(0);
    this.Vy0 = new Array(this.W*this.H).fill(0);

    // mouse object
    this.mouse = {
      update: true,
      pos: new Vec2(0, 0),
      vel: new Vec2(0, 0),
    }

    this.frameCount = 0;

    // start function
    this.init();
  }

  init() {
    this.app.stage.addChild(this.graphics);
    setGlobalVariables(this.W, this.H);
    this.initMouseEvents(false);
  }

  addDensity(x, y, amount) {
    const i = I(x, y);
    this.density[i] += amount;
  }

  addVelocity(x, y, amountX, amountY) {
    const i = I(x, y);
    this.Vx[i] += amountX;
    this.Vy[i] += amountY;
  }

  step() {
    let visc = this.visc;
    let diff = this.diff;
    let dt = this.dt;
    let Vx = this.Vx;
    let Vy = this.Vy;
    let Vx0 = this.Vx0;
    let Vy0  = this.Vy0;
    let s = this.s;
    let density = this.density;

    diffuse(1, Vx0, Vx, visc, dt);
    diffuse(2, Vy0, Vy, visc, dt);

    project(Vx0, Vy0, Vx, Vy);

    advect(1, Vx, Vx0, Vx0, Vy0, dt);
    advect(2, Vy, Vy0, Vx0, Vy0, dt);

    project(Vx, Vy, Vx0, Vy0);
    diffuse(0, s, density, diff, dt);
    advect(0, density, s, Vx, Vy, dt);
  }

  // function to render density
  render() {
    const g = this.graphics;
    g.clear();

    const SCALE = this.tileSize;
    for (let i = 0; i < this.W; i++) {
      for (let j = 0; j < this.H; j++) {
        let x = i * SCALE;
        let y = j * SCALE;
        let d = this.density[I(i, j)];
        const c = Math.min(255, d)
        g.beginFill(this.rgb(0, c, c));
        g.drawRect(x, y, SCALE, SCALE);
        g.endFill();
      }
    }
  }

  // renders points
  renderP() {
    const g = this.graphics;
    g.clear();

    const SCALE = this.tileSize;
    for (let i = 0; i < this.W; i++) {
      for (let j = 0; j < this.H; j++) {
        let x = i * SCALE;
        let y = j * SCALE;
        let d = this.density[I(i, j)];
        let vx = this.Vx[I(i, j)];
        let vy = this.Vy[I(i, j)];
        const c = Math.min(255, d)
        g.beginFill(this.rgb(255, 0, c))

        let nVx = vx > 0 ? Math.min(vx, SCALE) : vx < 0 ? Math.max(vx, -SCALE) : 0;
        let nVy = vy > 0 ? Math.min(vy, SCALE) : vy < 0 ? Math.max(vy, -SCALE) : 0;
        g.drawRect(x + nVx, y + nVy, 1, 1);
        g.endFill();
      }
    }
  }

    // renders points
    renderV() {
      const g = this.graphics;
      g.clear();
  
      const SCALE = this.tileSize;
      for (let i = 0; i < this.W; i++) {
        for (let j = 0; j < this.H; j++) {
          let x = i * SCALE;
          let y = j * SCALE;
          let d = this.density[I(i, j)];
          let vx = this.Vx[I(i, j)];
          let vy = this.Vy[I(i, j)];
          const c = Math.min(255, d)
          g.beginFill(this.rgb(255, 0, c))
  
          let nVx = vx > 0 ? Math.min(vx, SCALE) : vx < 0 ? Math.max(vx, -SCALE) : 0;
          let nVy = vy > 0 ? Math.min(vy, SCALE) : vy < 0 ? Math.max(vy, -SCALE) : 0;
          g.lineStyle(2).moveTo(x, y).lineTo(x + nVx, y + nVy);
          g.endFill();
        }
      }
    }

  initMouseEvents(clickingNeeded) {
    document.onmousemove = (e) => {
      const x = e.clientX;
      const y = e.clientY;
      this.updateMouse(x, y);
    }
    if (clickingNeeded) {
      this.mouse.update = false;

      document.onmouseleave = () => {
        this.mouse.update = false;
      }
      document.onmousedown = () => {
        this.mouse.update = true;
      }
      document.onmouseup = () => {
        this.mouse.update = false;
      }
    }
  }

  updateMouse(x, y) {
    if (!this.mouse.update) return;

    if (x >= 0 && y >= 0 && y < this.app.screen.height && x < this.app.screen.width) {

      const vel = this.mouse.pos.negate().addInPlace(x, y);
      this.mouse.vel = this.mouse.vel.add(vel);//this.mouse.vel.add(vel);
      this.mouse.vel.limit(this.tileSize*15);

      this.mouse.pos.x = x; this.mouse.pos.y = y;
    }
  }

  rgb(r, g, b) {
    return ((r << 16) + (g << 8) + b);
  }

  update(delta) {
    const mx = this.mouse.pos.x;
    const my = this.mouse.pos.y;
    const tileX = (mx-mx%this.tileSize)/this.tileSize;
    const tileY = (my-my%this.tileSize)/this.tileSize;

    // add density and velocity
    this.addDensity(tileX, tileY, this.mouse.vel.mag() * 30);
    this.addVelocity(tileX, tileY, this.mouse.vel.x, this.mouse.vel.y)

    if (this.frameCount % 1 == 0) {
      this.step();
    }
    this.render();

    // if mouse stays still its velocity lovers
    this.mouse.vel.multiply(0.8);

    this.frameCount++;
  }
}
